import React from "react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import * as L from "../styles/StyledLogin";
import axios from "axios";

const Login = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");
  const navigate = useNavigate();

  const handleSubmit = async (event) => {
    event.preventDefault();
    console.log(" ");

    try {
      const response = await axios.post(
        `${process.env.REACT_APP_API_URL}/accounts/login/` ||
          `http://127.0.0.1:8000/accounts/login/`,
        {
          username: username,
          password: password,
        }
      );
      console.log(" !");

      //    
      console.log(" :", response.data);

      const token = response.data.key || response.data.data?.key;
      if (token) {
        localStorage.setItem("token", response.data.key);
        console.log("  :", token);

        //   
        navigate("/");
      } else {
        console.log("   .");
        setMessage("   .");
      }
    } catch (error) {
      setMessage(error.message);
      if (error.response && error.response.status === 401) {
        console.log("   .");
        setMessage("   .");
      } else {
        console.log(" :", error.message);
        setMessage("  .");
      }
    }
  };

  const goJoin = () => {
    navigate(`/join`);
  };

  const goHome = () => {
    navigate(`/`);
  };

  const goFun = () => {
    navigate(`/funeral`);
  };

  const goMarket = () => {
    navigate(`/market`);
  };

  const goLib = () => {
    navigate("/library");
  };

  return (
    <L.Container>
      <header>
        <L.Nav>
          <L.NavContent>
            <L.Logo onClick={goHome}>
              <img
                id="logo"
                src={`${process.env.PUBLIC_URL}/images/logo.png`}
                alt="logo"
              />
            </L.Logo>
            <L.Menu>
              <L.MovingContent>
                <div id="library"> </div>
                <div id="bookroom" onClick={goLib}>
                  
                </div>
                <div id="comparison" onClick={goFun}>
                   
                </div>
                <div id="market" onClick={goMarket}>
                  
                </div>
              </L.MovingContent>
              <div id="bar"> | </div>
              <L.Account>
                <div id="login"></div>
                <div id="join" onClick={goJoin}>
                  
                </div>
              </L.Account>
            </L.Menu>
          </L.NavContent>
        </L.Nav>
      </header>

      <form onSubmit={handleSubmit}>
        <L.Content>
          <L.Firm>
            <img
              id="logo"
              src={`${process.env.PUBLIC_URL}/images/logo.png`}
              alt="logo"
            />
            <div id="detail">   </div>
          </L.Firm>
          <L.Login>
            <L.Id>
              <input
                id="id"
                type="id"
                name="username"
                placeholder=" "
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
              ></input>
            </L.Id>
            <L.Pw>
              <input
                id="pw"
                type="password"
                name="password"
                placeholder=" "
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              ></input>
            </L.Pw>
          </L.Login>
          {message && <L.Message>{message}</L.Message>}
          <L.Button type="submit">
            <button id="detail"></button>
            {/* <div id="detail"></div> */}
          </L.Button>
          <L.Condition>
            <div id="join" onClick={goJoin}>
              
            </div>
            {/* <div id="bar"> | </div>
            <div id="pw"> </div> */}
          </L.Condition>
        </L.Content>
      </form>

      <footer>
        <L.Footer>
          <L.Introduction>
            <div id="introduce">   </div>
            <img
              id="logo"
              src={`${process.env.PUBLIC_URL}/images/logo.png`}
              alt="logo"
            />
            <div id="team">  12 </div>
            <div id="name">, , , , </div>
            <L.Git>
              <img
                id="github"
                src={`${process.env.PUBLIC_URL}/images/Github.png`}
                alt=""
              />
              <a
                id="gitback"
                href="https://github.com/likelion12th-team2/starletter_BE"
                target="_blank"
                rel="noopener noreferrer"
              >
                BE
              </a>
              <div id="slash"> / </div>
              <a
                id="gitfront"
                href="https://github.com/likelion12th-team2/starletter_FE"
                target="_blank"
                rel="noopener noreferrer"
              >
                FE
              </a>
            </L.Git>
          </L.Introduction>
        </L.Footer>
      </footer>
    </L.Container>
  );
};

export default Login;
