import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import * as J from "../styles/StyledJoin";
import axios from "axios";
const Join = () => {
  const [password, setPassword] = useState("");
  const [username, setUsername] = useState("");
  const [name, setName] = useState("");
  const [nickname, setNickname] = useState("");
  const [password2, setPassword2] = useState("");
  const navigate = useNavigate();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [token, setToken] = useState("");
  const [message1, setMessage1] = useState("");
  const [message, setMessage] = useState("");

  useEffect(() => {
    const storedToken = localStorage.getItem("token");
    if (storedToken) {
      setToken(storedToken);
      setIsLoggedIn(true);
    }
  }, []);
  const handleSubmit = async (event) => {
    event.preventDefault();
    if (password !== password2) {
      setMessage("  .  .");
      return;
    }
    console.log("username:", username);
    console.log("Password:", password);
    console.log("Password2:", password2);
    console.log("name:", name);
    console.log("nickname:", nickname);
    try {
      await axios.post(
        `${process.env.REACT_APP_API_URL}/accounts/register` ||
          `http://127.0.0.1:8000/accounts/register/`,
        {
          username: username,
          password: password,
          name: name,
          nickname: nickname,
        }
      );
      navigate("/login");
    } catch (error) {
      if (error.response && error.response.data) {
        if (error.response.data.username) {
          setMessage(error.response.data.message);
        }
        if (error.response.data.nickname) {
          setMessage(error.response.data.message);
        }
        if (!error.response.data.username && !error.response.data.nickname) {
          setMessage(error.response.data.message);
        }
      } else {
        setMessage(error.response.data.message);
      }
    }
  };
  const goLogin = () => {
    navigate(`/login`);
  };
  const goHome = () => {
    navigate(`/`);
  };
  const goFun = () => {
    navigate(`/funeral`);
  };
  const goMarket = () => {
    navigate(`/market`);
  };
  // 
  const goMyBook = async () => {
    if (isLoggedIn) {
      try {
        //    
        const response = await axios.get(
          `${process.env.REACT_APP_API_URL}/mybooks/list/` ||
            `http://127.0.0.1:8000/mybooks/list/`,
          {
            headers: {
              Authorization: `Token ${token}`,
            },
          }
        );
        console.log("API :", response.data); //    
        if (
          response.data.books.length > 0 ||
          response.data.petsNoBook.length > 0
        ) {
          navigate(`/mybook/make`); //    ,   
        } else {
          navigate(`/mybook/addpet`); //    
        }
      } catch (error) {
        console.error("   :");
      }
    } else {
      navigate("/login");
    }
  };
  const goLib = () => {
    navigate("/library");
  };
  return (
    <J.Container>
      <header>
        <J.Nav>
          <J.NavContent>
            <J.Logo onClick={goHome}>
              <img
                id="logo"
                src={`${process.env.PUBLIC_URL}/images/logo.png`}
                alt="logo"
              />
            </J.Logo>
            <J.Menu>
              <J.MovingContent>
                <div id="library" onClick={goMyBook}>
                   
                </div>
                <div id="bookroom" onClick={goLib}>
                  
                </div>
                <div id="comparison" onClick={goFun}>
                   
                </div>
                <div id="market" onClick={goMarket}>
                  
                </div>
              </J.MovingContent>
              <div id="bar"> | </div>
              <J.Account>
                <div id="login" onClick={goLogin}>
                  
                </div>
                <div id="join"></div>
              </J.Account>
            </J.Menu>
          </J.NavContent>
        </J.Nav>
      </header>
      <form onSubmit={handleSubmit}>
        <J.Content>
          <J.Title>
            <div id="detail"></div>
          </J.Title>
          <J.Essential>
            <J.Id>
              <div id="id">*</div>
              <J.IdBox>
                <input
                  id="id"
                  type="id"
                  placeholder=" "
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                  required
                />
              </J.IdBox>
            </J.Id>
            <J.Pw>
              <div id="pw">*</div>
              <J.PwBox>
                <input
                  id="pw"
                  type="password"
                  placeholder="8  20  ,  "
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
              </J.PwBox>
            </J.Pw>
            <J.RePw>
              <div id="checkpw"> *</div>
              <J.ReBox>
                <input
                  id="checkpw"
                  type="password"
                  placeholder="   "
                  value={password2}
                  onChange={(e) => setPassword2(e.target.value)}
                  required
                />
              </J.ReBox>
            </J.RePw>
            <J.Name>
              <div id="name"> *</div>
              <J.NameBox>
                <input
                  id="name"
                  type="text"
                  placeholder="  "
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  required
                />
              </J.NameBox>
            </J.Name>
            <J.Nickname>
              <div id="nickname">*</div>
              <J.NickBox>
                <input
                  id="nickname"
                  type="text"
                  placeholder="  "
                  value={nickname}
                  onChange={(e) => setNickname(e.target.value)}
                  required
                />
              </J.NickBox>
            </J.Nickname>
          </J.Essential>
          {message && <J.Message>{message}</J.Message>}
          <J.Button type="submit">
            <button id="detail"></button>
          </J.Button>
        </J.Content>
      </form>
      <footer>
        <J.Footer>
          <J.Introduction>
            <div id="introduce">   </div>
            <img
              id="logo"
              src={`${process.env.PUBLIC_URL}/images/logo.png`}
              alt="logo"
            />
            <div id="team">  12 </div>
            <div id="name">, , , , </div>
            <J.Git>
              <img
                id="github"
                src={`${process.env.PUBLIC_URL}/images/Github.png`}
                alt=""
              />
              <a
                id="gitback"
                href="https://github.com/likelion12th-team2/starletter_BE"
                target="_blank"
                rel="noopener noreferrer"
              >
                BE
              </a>
              <div id="slash"> / </div>
              <a
                id="gitfront"
                href="https://github.com/likelion12th-team2/starletter_FE"
                target="_blank"
                rel="noopener noreferrer"
              >
                FE
              </a>
            </J.Git>
          </J.Introduction>
        </J.Footer>
      </footer>
    </J.Container>
  );
};
export default Join;
