import React from "react";
import { useState, useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import MyPageModal from "./MyPageModal";
import * as H from "../styles/StyledHome";
import axios from "axios";

const Home = () => {
  const navigate = useNavigate();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const myPageRef = useRef(null);
  const [token, setToken] = useState("");

  useEffect(() => {
    const storedToken = localStorage.getItem("token");
    if (storedToken) {
      setToken(storedToken);
      setIsLoggedIn(true);
    }
  }, []);

  const goLogin = () => {
    navigate(`/login`);
  };

  const goJoin = () => {
    navigate(`/join`);
  };

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const handleLogout = async () => {
    try {
      const response = await axios.post(
        `${process.env.REACT_APP_API_URL}/accounts/logout/` ||
          `http://127.0.0.1:8000/accounts/logout/`,
        {},
        {
          headers: {
            Authorization: `Token ${token}`, //    
          },
        }
      );
      console.log(" :", response.data);
      //        //
      localStorage.removeItem("token");
      localStorage.removeItem("key");
      setIsLoggedIn(false);
      setToken("");
      navigate(`/`);
    } catch (error) {
      console.error(" :", error);
    }
  };

  const goHome = () => {
    navigate(`/`);
  };

  const goFun = () => {
    navigate(`/funeral`);
  };

  const goMarket = () => {
    navigate(`/market`);
  };

  // 
  const goMyBook = async () => {
    if (isLoggedIn) {
      try {
        //    
        const response = await axios.get(
          `${process.env.REACT_APP_API_URL}/mybooks/list/` ||
            `http://127.0.0.1:8000/mybooks/list/`,
          {
            headers: {
              Authorization: `Token ${token}`,
            },
          }
        );
        console.log("API :", response.data); //    
        if (
          response.data.books.length > 0 ||
          response.data.petsNoBook.length > 0
        ) {
          navigate(`/mybook/make`); //    ,   
        } else {
          navigate(`/mybook/addpet`); //    
        }
      } catch (error) {
        console.error("   :");
      }
    } else {
      navigate("/login");
    }
  };

  const goLib = async () => {
    navigate("/library");
  };

  return (
    <H.Container>
      <header>
        <H.Nav>
          <H.NavContent>
            <H.Logo onClick={goHome}>
              <img
                id="logo"
                src={`${process.env.PUBLIC_URL}/images/logo.png`}
                alt="logo"
              />
            </H.Logo>
            <H.Menu>
              <H.MovingContent>
                <div id="library" onClick={goMyBook}>
                   
                </div>
                <div id="bookroom" onClick={goLib}>
                  
                </div>
                <div id="comparison" onClick={goFun}>
                   
                </div>
                <div id="market" onClick={goMarket}>
                  
                </div>
              </H.MovingContent>
              <div id="bar"> | </div>
              <H.Account>
                {isLoggedIn ? (
                  <>
                    <div id="mypage" onClick={openModal} ref={myPageRef}>
                      
                    </div>
                    <div id="logout" onClick={handleLogout}>
                      
                    </div>
                  </>
                ) : (
                  <>
                    <div id="login" onClick={goLogin}>
                      
                    </div>
                    <div id="join" onClick={goJoin}>
                      
                    </div>
                  </>
                )}
              </H.Account>
            </H.Menu>
          </H.NavContent>
        </H.Nav>
      </header>
      {/*   */}
      <MyPageModal
        isOpen={isModalOpen}
        onClose={closeModal}
        anchorRef={myPageRef}
      />
      {/*  */}
      <H.Body>
        <H.List>
          <H.Lib onClick={goMyBook}>
            <img
              id="library"
              src={`${process.env.PUBLIC_URL}/images/Lib.svg`}
              alt=""
            />
            <div id="detlib"> </div>
          </H.Lib>
          <H.Book onClick={goLib}>
            <img
              id="bookroom"
              src={`${process.env.PUBLIC_URL}/images/Book.svg`}
              alt=""
            />
            <div id="detbook"></div>
          </H.Book>
          <H.Fun onClick={goFun}>
            <img
              id="funeral"
              src={`${process.env.PUBLIC_URL}/images/Funeral.svg`}
              alt=""
            />
            <div id="detfun"></div>
          </H.Fun>
          <H.Mar onClick={goMarket}>
            <img
              id="market"
              src={`${process.env.PUBLIC_URL}/images/Market.svg`}
              alt=""
            />
            <div id="detmar"></div>
          </H.Mar>
        </H.List>
        <img
          id="main"
          src={`${process.env.PUBLIC_URL}/images/Main.svg`}
          alt=""
        />
      </H.Body>
      <footer>
        <H.Footer>
          <H.Introduction>
            <div id="introduce">   </div>
            <img
              id="logo"
              src={`${process.env.PUBLIC_URL}/images/logo.png`}
              alt="logo"
            />
            <div id="team">  12 </div>
            <div id="name">, , , , </div>
            <H.Git>
              <img
                id="github"
                src={`${process.env.PUBLIC_URL}/images/Github.png`}
                alt=""
              />
              <a
                id="gitback"
                href="https://github.com/likelion12th-team2/starletter_BE"
                target="_blank"
                rel="noopener noreferrer"
              >
                BE
              </a>
              <div id="slash"> / </div>
              <a
                id="gitfront"
                href="https://github.com/likelion12th-team2/starletter_FE"
                target="_blank"
                rel="noopener noreferrer"
              >
                FE
              </a>
            </H.Git>
          </H.Introduction>
        </H.Footer>
      </footer>
    </H.Container>
  );
};

export default Home;
