import React, { useState, useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import MyPageModal from "./MyPageModal";
import * as E from "../styles/StyledEdit";
import axios from "axios";

const EditProfile = () => {
  const navigate = useNavigate();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const myPageRef = useRef(null);
  const [name, setName] = useState("");
  const [name1, setName1] = useState("");
  const [nickname, setNickname] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [slr, setSlr] = useState("");

  const handleNameChange = (e) => {
    setName(e.target.value);
  };

  const handleNicknameChange = (e) => {
    setNickname(e.target.value);
  };

  useEffect(() => {
    //    (: localStorage   )
    const storedToken = localStorage.getItem("token");
    if (storedToken) {
      console.log(" ");
      setIsLoggedIn(true);
    }
  }, []);

  const key = localStorage.getItem("token");

  const goHome = () => {
    navigate(`/`);
  };

  const goLogin = () => {
    navigate(`/login`);
  };

  const goJoin = () => {
    navigate(`/join`);
  };

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const goMyBook = async () => {
    if (isLoggedIn) {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_API_URL}/mybooks/list/` ||
            `http://127.0.0.1:8000/mybooks/list/`,
          {
            headers: {
              Authorization: `Token ${key}`,
            },
          }
        );
        console.log("API :", response.data);
        if (
          response.data.books.length > 0 ||
          response.data.petsNoBook.length > 0
        ) {
          navigate(`/mybook/make`);
        } else {
          navigate(`/mybook/addpet`);
        }
      } catch (error) {
        console.error("   :", error);
      }
    } else {
      navigate("/login");
    }
  };

  const goLib = () => {
    if (isLoggedIn) {
      navigate("/library");
    } else {
      navigate("/login");
    }
  };

  const handleLogout = async () => {
    try {
      const response = await axios.post(
        `${process.env.REACT_APP_API_URL}/accounts/logout/` ||
          `http://127.0.0.1:8000/accounts/logout/`,
        {},
        {
          headers: {
            Authorization: `Token ${key}`, //    
          },
        }
      );
      console.log(" :", response.data);
      //        
      localStorage.removeItem("token");
      setIsLoggedIn(false);
      navigate(`/`);
    } catch (error) {
      console.error(" :", error);
    }
  };

  const goFun = () => {
    navigate(`/funeral`);
  };

  const goMarket = () => {
    navigate(`/market`);
  };

  const handleSubmit = async (e) => {
    e.preventDefault(); //     
    console.log("Form submitted"); //    
    try {
      const response = await axios.put(
        `${process.env.REACT_APP_API_URL}/accounts/myinfo/` ||
          `http://127.0.0.1:8000/accounts/myinfo/`,
        {
          name: name || name1,
          nickname: nickname || slr,
        },
        {
          headers: {
            Authorization: `Token ${key}`, //    
          },
        }
      );
      console.log("Profile updated:", response.data);
      setErrorMessage(""); //      
      navigate(`/`);
    } catch (error) {
      console.error("Error response:", error.response);
      if (
        error.response &&
        error.response.data &&
        error.response.data.errorMessage
      ) {
        setErrorMessage(error.response.data.errorMessage); //     
      } else {
        setErrorMessage("    ."); //    
      }
    }
  };

  useEffect(() => {
    const fetchName = async () => {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_API_URL}/accounts/myinfo/` ||
            `http://127.0.0.1:8000/accounts/myinfo/`,
          {
            headers: {
              Authorization: `Token ${key}`, //     
            },
          }
        );
        setName1(response.data.name);
        setSlr(response.data.nickname);
      } catch (error) {
        console.error("Error fetching name:", error);
        setErrorMessage("     .");
      }
    };

    fetchName();
  }, [key]);

  return (
    <E.Container>
      <header>
        <E.Nav>
          <E.NavContent>
            <E.Logo onClick={goHome}>
              <img
                id="logo"
                src={`${process.env.PUBLIC_URL}/images/logo.png`}
                alt="logo"
              />
            </E.Logo>
            <E.Menu>
              <E.MovingContent>
                <div id="library" onClick={goMyBook}>
                   
                </div>
                <div id="bookroom" onClick={goLib}>
                  
                </div>
                <div id="comparison" onClick={goFun}>
                   
                </div>
                <div id="market" onClick={goMarket}>
                  
                </div>
              </E.MovingContent>
              <div id="bar"> | </div>
              <E.Account>
                {isLoggedIn ? (
                  <>
                    <div id="mypage" onClick={openModal} ref={myPageRef}>
                      
                    </div>
                    <div id="logout" onClick={handleLogout}>
                      
                    </div>
                  </>
                ) : (
                  <>
                    <div id="login" onClick={goLogin}>
                      
                    </div>
                    <div id="join" onClick={goJoin}>
                      
                    </div>
                  </>
                )}
              </E.Account>
            </E.Menu>
          </E.NavContent>
        </E.Nav>
      </header>
      <MyPageModal
        isOpen={isModalOpen}
        onClose={closeModal}
        anchorRef={myPageRef}
      />
      <form onSubmit={handleSubmit}>
        <E.Body>
          <E.Title>, {name1} </E.Title>
          <E.Profile>
            <E.Name>
              <div id="name"></div>
              <E.DetName>
                <input
                  id="inname"
                  type="text"
                  value={name || name1}
                  onChange={handleNameChange}
                />
              </E.DetName>
            </E.Name>
            <E.Nickname>
              <div id="nickname"></div>
              <E.DetNick>
                <input
                  id="innick"
                  type="text"
                  value={nickname || slr}
                  onChange={handleNicknameChange}
                />
              </E.DetNick>
            </E.Nickname>
          </E.Profile>
          {errorMessage && <E.ErrorMessage>{errorMessage}</E.ErrorMessage>}
          <E.Button>
            <button id="btn" type="submit">
              
            </button>
          </E.Button>
        </E.Body>
      </form>
      <footer>
        <E.Footer>
          <E.Introduction>
            <div id="introduce">   </div>
            <img
              id="logo"
              src={`${process.env.PUBLIC_URL}/images/logo.png`}
              alt="logo"
            />
            <div id="team">  12 </div>
            <div id="name">, , , , </div>
            <E.Git>
              <img
                id="github"
                src={`${process.env.PUBLIC_URL}/images/Github.png`}
                alt=""
              />
              <a
                id="gitback"
                href="https://github.com/likelion12th-team2/starletter_BE"
                target="_blank"
                rel="noopener noreferrer"
              >
                BE
              </a>
              <div id="slash"> / </div>
              <a
                id="gitfront"
                href="https://github.com/likelion12th-team2/starletter_FE"
                target="_blank"
                rel="noopener noreferrer"
              >
                FE
              </a>
            </E.Git>
          </E.Introduction>
        </E.Footer>
      </footer>
    </E.Container>
  );
};

export default EditProfile;
