import React, { useState, useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import MyPageModal from "./MyPageModal";
import * as L from "../styles/styledLibrary";
import axios from "axios";
import styled from "styled-components";

//        
const Search = styled(({ isAbsolute, ...props }) => <L.Search {...props} />)`
  position: ${(props) => (props.isAbsolute ? "absolute" : "static")};
`;

const Library = ({ nickname }) => {
  const navigate = useNavigate();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isSearchModalOpen, setIsSearchModalOpen] = useState(false); // State for SearchPlus modal 
  const [isSearchAbsolute, setIsSearchAbsolute] = useState(false); // State for L.Search position 
  const [searchResults, setSearchResults] = useState([]); //     
  const [isSearchExecuted, setIsSearchExecuted] = useState(false); //     
  const [booksMostMinds, setBooksMostMinds] = useState([]); //      
  const [booksRecent, setBooksRecent] = useState([]); //    
  const myPageRef = useRef(null);
  const searchRef = useRef(null); // Ref for search input
  const searchWrapRef = useRef(null); // Ref for search wrap 
  const [token, setToken] = useState("");
  const [recentSearches, setRecentSearches] = useState([]); //  
  const [selectedKeyword, setSelectedKeyword] = useState(""); //  

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token) {
      console.log(" ");
      setIsLoggedIn(true);
      setToken(token);
    }
    //   
    const storedSearches =
      JSON.parse(localStorage.getItem("recentSearches")) || [];
    setRecentSearches(storedSearches);

    //    
    const handleClickOutside = (event) => {
      if (
        searchWrapRef.current &&
        !searchWrapRef.current.contains(event.target)
      ) {
        setIsSearchModalOpen(false);
        setIsSearchAbsolute(false); //    position static 
      }
    };
    document.addEventListener("mousedown", handleClickOutside);

    return () => {
      //      
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  const key = localStorage.getItem("token");

  useEffect(() => {
    LibraryBooks();
  }, []);

  const LibraryBooks = async () => {
    try {
      const response = await axios.get(
        `${process.env.REACT_APP_API_URL}/bookshelf/` ||
          `http://127.0.0.1:8000/bookshelf/`
      );
      console.log("API :", response.data); //    
      setBooksMostMinds(response.data.booksMostMinds);
      setBooksRecent(response.data.booksRecent);
    } catch (error) {
      console.error("   :", error);
      console.log(error.response); //    
    }
  };

  const goHome = () => {
    navigate(`/`);
  };

  const goLogin = () => {
    navigate(`/login`);
  };

  const goJoin = () => {
    navigate(`/join`);
  };

  const goMyBookDetail = (bookId) => {
    navigate(`/library/${bookId}`, { state: { bookId } });
  };

  const goFun = () => {
    navigate(`/funeral`);
  };

  const goMarket = () => {
    navigate(`/market`);
  };

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  //  
  const goMyBook = async () => {
    if (isLoggedIn) {
      try {
        //    
        const response = await axios.get(
          `${process.env.REACT_APP_API_URL}/mybooks/list/` ||
            `http://127.0.0.1:8000/mybooks/list/`,
          {
            headers: {
              Authorization: `Token ${token}`,
            },
          }
        );
        console.log("API :", response.data); //    
        if (
          response.data.books.length > 0 ||
          response.data.petsNoBook.length > 0
        ) {
          navigate(`/mybook/make`); //    ,   
        } else {
          navigate(`/mybook/addpet`); //    
        }
      } catch (error) {
        console.error("   :");
      }
    } else {
      navigate("/login");
    }
  };

  const goLib = () => {
    navigate("/library");
  };

  const handleLogout = async () => {
    try {
      const response = await axios.post(
        `${process.env.REACT_APP_API_URL}/accounts/logout/` ||
          `http://127.0.0.1:8000/accounts/logout/`,
        {},
        {
          headers: {
            Authorization: `Token ${key}`, //    
          },
        }
      );
      console.log(" :", response.data);
      //        
      localStorage.removeItem("token");
      localStorage.removeItem("key");
      setIsLoggedIn(false);
      setToken("");
      navigate(`/`);
    } catch (error) {
      console.error(" :", error);
    }
  };

  const profile = {
    // image: 'path_to_profile_image.jpg',
    name: nickname,
  };

  //  
  const saveRecentSearch = (keyword) => {
    const updatedSearches = [
      keyword,
      ...recentSearches.filter((search) => search !== keyword),
    ].slice(0, 7); //  7 
    setRecentSearches(updatedSearches);
    localStorage.setItem("recentSearches", JSON.stringify(updatedSearches));
  };

  const handleSearch = async () => {
    const searchValue = searchRef.current.value;
    if (searchValue) {
      saveRecentSearch(searchValue);
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_API_URL}/bookshelf/?search=${searchValue}` ||
            `http://127.0.0.1:8000/bookshelf/?search=${searchValue}`
        );
        console.log(" :", response.data); //    
        setSearchResults(response.data.searchedBooks); //    
        setIsSearchExecuted(true); //     
        setIsSearchModalOpen(false); //    
        setIsSearchAbsolute(false); //    position static 
      } catch (error) {
        console.error(" :", error);
        console.log(error.response); //    
      }
    }
  };

  useEffect(() => {
    console.log("  state :", searchResults);
  }, [searchResults]);

  const handleKeywordClick = (keyword) => {
    setSelectedKeyword(keyword);
    if (searchRef.current) {
      searchRef.current.value = keyword;
      handleSearch();
    }
  };

  return (
    <L.Container>
      <header>
        <L.Nav>
          <L.NavContent>
            <L.Logo onClick={goHome}>
              <img
                id="logo"
                src={`${process.env.PUBLIC_URL}/images/logo.png`}
                alt="logo"
              />
            </L.Logo>
            <L.Menu>
              <L.MovingContent>
                <div id="library" onClick={goMyBook}>
                   
                </div>
                <div id="bookroom" onClick={goLib}>
                  
                </div>
                <div id="comparison" onClick={goFun}>
                   
                </div>
                <div id="market" onClick={goMarket}>
                  
                </div>
              </L.MovingContent>
              <div id="bar"> | </div>
              <L.Account>
                {isLoggedIn ? (
                  <>
                    <div id="mypage" onClick={openModal} ref={myPageRef}>
                      
                    </div>
                    <div id="logout" onClick={handleLogout}>
                      
                    </div>
                  </>
                ) : (
                  <>
                    <div id="login" onClick={goLogin}>
                      
                    </div>
                    <div id="join" onClick={goJoin}>
                      
                    </div>
                  </>
                )}
              </L.Account>
            </L.Menu>
          </L.NavContent>
        </L.Nav>
      </header>
      <L.Main>
        <L.LibContainer>
          <L.SearchWrap ref={searchWrapRef}>
            {" "}
            {/* searchWrapRef  */}
            <Search isAbsolute={isSearchAbsolute}>
              <img
                id="searchImg"
                src={`${process.env.PUBLIC_URL}/images/libSearch.png`}
                alt="search"
              />
              <input
                id="search"
                type="text"
                placeholder="   "
                ref={searchRef}
                onFocus={() => {
                  setIsSearchModalOpen(true);
                  setIsSearchAbsolute(true); //    position absolute 
                }} // onFocus   
                onKeyPress={(e) => {
                  if (e.key === "Enter") {
                    handleSearch();
                  }
                }}
              />
            </Search>
            {isSearchModalOpen && ( // isSearchModalOpen    
              <L.SearchPlus>
                <L.RecentTitle> </L.RecentTitle>
                <L.RecentList>
                  {recentSearches.map((search, index) => (
                    <div key={index} className="recent-search">
                      {search}
                    </div>
                  ))}
                </L.RecentList>
                <L.RecommendTitle> </L.RecommendTitle>
                <L.RecommendList>
                  {["", "", "", ""].map((keyword) => (
                    <button
                      key={keyword}
                      className={`keyword ${
                        selectedKeyword === keyword ? "selected" : ""
                      }`}
                      id={keyword}
                      onClick={() => handleKeywordClick(keyword)}
                    >
                      #{keyword}
                    </button>
                  ))}
                </L.RecommendList>
              </L.SearchPlus>
            )}
          </L.SearchWrap>

          {/*     */}
          {!isSearchExecuted && (
            <L.LibMain>
              <L.Heart>
                <L.Title>   </L.Title>
                <L.HeartWrap>
                  {/*  */}
                  {booksMostMinds.slice(0, 5).map((bookMostMinds) => (
                    <L.Book
                      key={bookMostMinds.id}
                      onClick={() => {
                        goMyBookDetail(bookMostMinds.id);
                      }}
                    >
                      <L.BookCover>
                        <L.BookCoverImg>
                          <img
                            id="MycoverImg"
                            src={
                              bookMostMinds.cover ||
                              `${process.env.PUBLIC_URL}/images/bookCover.png`
                            }
                            alt="Mycover1"
                          />
                        </L.BookCoverImg>
                        <L.BookCoverText>
                          <div id="title">{bookMostMinds.title}</div>
                        </L.BookCoverText>
                      </L.BookCover>
                      <L.BookTitle>{bookMostMinds.title}</L.BookTitle>
                      <L.BookWriter>{bookMostMinds.author}</L.BookWriter>
                    </L.Book>
                  ))}
                </L.HeartWrap>
              </L.Heart>
              <L.Recent>
                <L.Title> </L.Title>
                <L.RecentWrap>
                  {/*  */}
                  {booksRecent.slice(0, 5).map((bookRecent) => (
                    <L.Book
                      key={bookRecent.id}
                      onClick={() => {
                        goMyBookDetail(bookRecent.id);
                      }}
                    >
                      <L.BookCover>
                        <L.BookCoverImg>
                          <img
                            id="MycoverImg"
                            src={
                              bookRecent.cover ||
                              `${process.env.PUBLIC_URL}/images/bookCover.png`
                            }
                            alt="Mycover1"
                          />
                        </L.BookCoverImg>
                        <L.BookCoverText>
                          <div id="title">{bookRecent.title}</div>
                        </L.BookCoverText>
                      </L.BookCover>
                      <L.BookTitle>{bookRecent.title}</L.BookTitle>
                      <L.BookWriter>{bookRecent.author}</L.BookWriter>
                    </L.Book>
                  ))}
                </L.RecentWrap>
              </L.Recent>
            </L.LibMain>
          )}
          {/*     */}
          {isSearchExecuted && (
            <L.SM>
              {searchResults.length > 0 ? (
                <L.SMBooks>
                  {searchResults.map((book) => (
                    <L.Book
                      key={book.id}
                      onClick={() => {
                        goMyBookDetail(book.id);
                      }}
                    >
                      <L.BookCover>
                        <L.BookCoverImg>
                          <img
                            id="MycoverImg"
                            src={
                              book.cover ||
                              `${process.env.PUBLIC_URL}/images/bookCover.png`
                            }
                            alt="Mycover"
                          />
                        </L.BookCoverImg>
                        <L.BookCoverText>
                          <div id="title">{book.title}</div>
                        </L.BookCoverText>
                      </L.BookCover>
                      <L.BookTitle>{book.title}</L.BookTitle>
                      <L.BookWriter>{book.author}</L.BookWriter>
                    </L.Book>
                  ))}
                </L.SMBooks>
              ) : (
                <L.NoResults>
                  <div>  </div>
                </L.NoResults>
              )}
            </L.SM>
          )}
        </L.LibContainer>
      </L.Main>
      <MyPageModal
        isOpen={isModalOpen}
        onClose={closeModal}
        profile={profile}
        anchorRef={myPageRef}
      />
      <footer>
        <L.Footer>
          <L.Introduction>
            <div id="introduce">   </div>
            <img
              id="logo"
              src={`${process.env.PUBLIC_URL}/images/logo.png`}
              alt="logo"
            />
            <div id="team">  12 </div>
            <div id="name">, , , , </div>
            <L.Git>
              <img
                id="github"
                src={`${process.env.PUBLIC_URL}/images/Github.png`}
                alt=""
              />
              <a
                id="gitback"
                href="https://github.com/likelion12th-team2/starletter_BE"
                target="_blank"
                rel="noopener noreferrer"
              >
                BE
              </a>
              <div id="slash"> / </div>
              <a
                id="gitfront"
                href="https://github.com/likelion12th-team2/starletter_FE"
                target="_blank"
                rel="noopener noreferrer"
              >
                FE
              </a>
            </L.Git>
          </L.Introduction>
        </L.Footer>
      </footer>
    </L.Container>
  );
};

export default Library;
